import React from 'react';
import { Doughnut} from 'react-chartjs-2';

import styles from './Pie.module.css';
const state = {
  labels: ['Confirmed', 'Recovered', 'Deaths'],
  datasets: [
    {
      label: 'Rainfall',
      backgroundColor: [
        '#2FDE00',
        '#C9DE00',
        '#B21F00',
      ],
      hoverBackgroundColor: [
        '#2FDE00',
        '#C9DE00',
        '#B21F00',
      ],
      data: [50, 45, 5]
    }
  ]
}
const PieChart = () => {
    return (
      <div className={ styles.container }>

        <Doughnut 
        
          data={state}
          options={{
            legend:{
              display:true,
              position:'right'
            }
          }}
        />
      </div>
    );
  }


export default PieChart;